import { useState } from "react";
import { useForm } from "react-hook-form";
import HCaptcha from "@hcaptcha/react-hcaptcha";
export default function Contact() {
  const { register, handleSubmit, setValue } = useForm();

  const onHCaptchaChange = (token) => {
    setValue("h-captcha-response", token);
  };

  const [result, setResult] = useState("");

  const onSubmit = async (event) => {
    event.preventDefault();
    setResult("Sending....");
    const formData = new FormData(event.target);

    formData.append("access_key", "09e461ab-ad75-4ef3-8dc0-0497c9329c57");

    const response = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      body: formData,
    });

    const data = await response.json();

    if (data.success) {
      setResult("Form Submitted Successfully");
      event.target.reset();
    } else {
      console.log("Error", data);
      setResult(data.message);
    }
  };
  return (
    <div className="Main-form justify-content-center">
      <form onSubmit={onSubmit}>
        <div className="container align-items-center ">
          <div className="row align-items-center">
            <label htmlFor="validationTooltip01" className="form-label pb-2">
              Name:
            </label>
            <input
              type="text"
              className="form-control mb-3"
              id="validationTooltip01"
              placeholder="Enter Your Name"
              required
            />

            <label htmlFor="validation02" className="form-label pb-2">
              E-mail:
            </label>
            <input
              type="email"
              name="email"
              id="validation02"
              placeholder="Enter Your E-mail Address"
              className="form-control mb-3"
              required
            />
            <label htmlFor="validation03" className="form-label pb-2">
              Message:
            </label>
            <textarea
              name="message"
              rows={5}
              id="validation03"
              placeholder="Send your valuable messages or request to us"
              className="form-control"
              required
            ></textarea>
            <input type="checkbox" name="botcheck" className="hidden " />
            <div className="d-grid pt-5">
              <button className="btn btn-primary" type="submit">
                Submit Form
              </button>
            </div>
          </div>
        </div>
      </form>
      <span>{result}</span>
    </div>
  );
}
